.announcement {
  max-width: 590px;
  position: relative;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding: 20px 20px 0px 20px;
}
.announcementTitle {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  margin: 0;
  color: #161e54;
}
.date {
  width: 145px;
  height: 27px;
  padding: 0px 6px 0px 6px;

  border-radius: 4px;
  border: 1px solid #efefef;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.today {
  font-size: 12px;
  font-weight: 400;
  line-height: 28px;
}
.a-table {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.activity {
  width: 630px;
  text-align: center;

  border-radius: 0px 0px 10px 10px;
  outline: 1px solid #e0e0e0;
  color: #ff5151;
  box-sizing: border-box;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  margin: 0;
}
