.dashboard {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  width: 640px;
  margin-top: 25px;
  margin-left: 50px;
}
.dashboadTitle {
  width: 640px;
  padding: 10px 50px;
}
.title {
  width: 100%;
  font-weight: 500;
  font-size: 24px;
  line-height: 42px;
  color: #161e54;
}

.dashboard-items {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
}
.dashboardCard2 {
  display: flex;
  align-items: center;
  gap: 20px;
  width: 100%;
  margin-top: 10px;
}
.ann {
  width: 100%;
}
