.sidebar {
  padding: 30px;
  width: 17%;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 25px;
  background-color: #e0e0e0;
}
.mainmenu {
  width: 100%;
  height: 318px;
  padding: 10px, 0px, 10px, 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0px;
}
.logo {
  margin-left: 10px;
}
.heading1 {
  width: 100%;
  height: 38px;
  padding: 10px, 0px, 10px, 0px;
}
.menubar {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #686868;
}
.mainLinks {
  display: flex;
  align-items: center;
  width: 100%;
  height: 56px;
  gap: 24px;
}
.mainMenuLink {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #686868;
}
.mainMenuLink:hover {
  cursor: pointer;
}
.svgIcon {
  fill: #b2b2b2;
}

.mainLinks:hover .svgIcon {
  fill: #ff5151; /* New SVG color on hover */
}
.mainLinks:hover .mainMenuLink {
  color: #ff5151; /* New text color on hover */
}
